@import "../inc/config";
.uk-offcanvas-overlay::before {
  background: rgba($color0, .9);
}
#acc {
  .uk-offcanvas-bar {
    position: relative;
    height: 100vh;
    z-index: 9999;
    background: $color1;
    padding: 0;
  }

  .uk-close {
    color: #fff;
  }

  .account-bar {
    height: 100vh;
    display: flex;
    flex-direction: column;

    .line-user {
      padding: 0 15px;
      display: flex;
      align-items: center;
      height: 60px;
      background: darken($color1, 2);

      .avatar {
        size: 40px;
        border-radius: 50%;
      }

      .name {
        margin-left: 20px;
        color: #fff;
        letter-spacing: 1px;
        text-transform: uppercase;
        font: 600 12px/16px $font;
      }
    }

    .menu {
      height: 100px;
      display: flex;
      flex-direction: column;
      width: 100%;

      li {
        padding: 15px 30px;
        width: 100%;

        a {
          color: #fff;
          letter-spacing: 1px;
          text-transform: uppercase;
          font: 600 12px/16px $font;
        }

      }

      &.exit {
        flex: 1;
        flex-direction: column-reverse;

        li a {
          color: $color2;
        }
      }
    }
  }
}
